import * as Util from "util";
import { DefaultObject, isBang } from "../../sdk";
import type { IInletsMeta, IOutletsMeta, IArgsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";

export class Print extends DefaultObject<{}, {}, [any], [], [string], {}> {
    static package = "Example Package";
    static author = "Your Name";
    static version = "1.0";
    static description = "Addition operation";
    static inlets: IInletsMeta = [
        {
            isHot: true,
            type: "anything",
            description: "The item to print"
        },
    ];
    static args: IArgsMeta = [{
        type: "string",
        optional: true,
        default: "Print",
        description: "Title"
    }];
    get title() {
        return `${this.args[0] || "Print"}`;
    }
    subscribe() {
        super.subscribe();
        this.on("preInit", () => {
            this.inlets = 1;
            this.outlets = 0;
        });
        this.on("inlet", ({ data, inlet }) => {
            if (inlet === 0) {
                if (isBang(data)) {
                    this.patcher.newLog("none", this.title, "Bang", this.box);
                } else {
                    this.patcher.newLog("none", this.title, typeof data === "string" ? data : Util.inspect(data), this.box);
                }
            }
        });
    }
}
